.App {
  text-align: center;
  border: 1px solid black;
  height: 99vh;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: white;

}

@media screen and (min-width: 768px) {
  .App {
    width: 60vw;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1024px) {
  .App {
    width: 50vw;
  }
}





